import React from "react";
import HomeIcon from '@material-ui/icons/Home';
import Button from "components/CustomButtons/Button.js";
import ListItem from "@material-ui/core/ListItem";
import Tooltip from "@material-ui/core/Tooltip";
import ContactMailIcon from '@material-ui/icons/ContactMail';
import StarIcon from '@material-ui/icons/Star';
import People from '@material-ui/icons/People';
import ReactCountryFlag from "react-country-flag";
//principal
//nosotros
export default function Link (props) {

	const renderIcon = (registro) => {
		switch(registro.icon){
			case "HomeIcon":
				return(
							<React.Fragment>
					          <HomeIcon className={classes.icons} /> {registro.titulo}
							</React.Fragment>
				)
				break;
				case "People":
				return(
							<React.Fragment>
					          <People className={classes.icons} /> {registro.titulo}
							</React.Fragment>
				)
				break;
				case "ContactMailIcon":
				return(
							<React.Fragment>
					          <ContactMailIcon className={classes.icons} /> {registro.titulo}
							</React.Fragment>
				)
				break;								
				default :
					return(
								<React.Fragment>
						          <HomeIcon className={classes.icons} /> {registro.titulo}
								</React.Fragment>
					)
				break;
		}

	}

	const {classes,config} = props;

	const renderButton = (registro) => {
		if(registro.button && registro.button.traducir){
			let code = registro.button.traducir;
			// console.log(code)
			return(
				          <Button
				            id={registro.button.id} 
				            href={registro.button.href}
				            color="transparent"
				            className={classes.navLink}
				            onClick={(e) => {
				            		localStorage.setItem("idioma",registro.button.traducir)
				            }}
				          >
				            <ReactCountryFlag 
							    styleProps={{
							        width: '20px',
							        height: '20px'
							    }}				            	
				            	code={code}
				            	svg/>
				          </Button>
			)
		}else{
			// console.log("no entro")
			return(
			          <Button
			            id={registro.button.id} 
			            href={registro.button.href}
			            color="transparent"
			            className={classes.navLink}
			          >
			            <i className={classes.socialIcons +" "+ registro.icon} />
			          </Button>			
				)
		}
	}
	const bodyLink = (registro,type="default") => {
		switch(type){
			case "default":
			return(
				      <ListItem className={classes.listItem} key={"index_" + registro.titulo} >
				        <Button
				          id={registro.id}
				          href="#"
				          color="transparent"
				          className={classes.navLink}
				        >
				          {renderIcon(registro)}
				        </Button>	
			      	</ListItem>
			)
			break;
			case "social":
				return(
				      <ListItem className={classes.listItem} key={"index_" + registro.titulo} >
							<Tooltip
				          id={registro.tooltip.id}
				          title={registro.tooltip.titulo}
				          placement={window.innerWidth > 959 ? "top" : "left"}
				          classes={{ tooltip: classes.tooltip }}
				        >
						{renderButton(registro)}
				        </Tooltip>	
			      	</ListItem>
				)
			break;
			default:
			break;
		}
	}
	const renderLink = () => {
				let link = config.map( (registro,index) => {
					let Tag = registro.icon;
				      return bodyLink(registro,registro.type);
					})
				return link;
	}

	return(
				<div>{renderLink()}</div>
			)
} 