export default function CustomCard2(props) {
    const classesList = useStylesList();
    const classes = useStyles();  
    const {img,introduccion,titulo,lista,icon} = props.contenido;
    let islista = true;
    const icono = (icon) => {
      let icon_=""
      switch(icon){
        case "mision":
          icon =VerifiedUser
          break;
        case "vision":
          icon=Visibility
          break;
        case "valores":
          icon=Favorite
          break;          
          default:
          break
      }
      return icon
    }
    const Lista = (lista,classesList) => {
    if(lista){
          // console.log(lista)
          let ui = lista.map( (list,index) =>{
            if(islista)
            return(
                <List component="nav"  className={classesList.root} aria-label="contacts" key={"_"+index}>
                  <ListItem button>
                    <ListItemIcon>
                      <SendIcon />
                    </ListItemIcon>
                    <ListItemText primary={list} />
                  </ListItem>
                </List>
            )
            return(
              <div>
                  <Chip
                    icon={<FaceIcon />}
                    label="Primary clickable"
                    clickable
                    color="success"
                    deleteIcon={<DoneIcon />}
                    variant="outlined"
                    key={index}
                  />
              </div>
            )

          })  
          return ui;
      }
    }

  return (
    <div className={classes.clearfix}>
      <Card className={classes.card}>
        <CardMedia
          className={classes.media}
          image={
            img
          }
        />
            {/*"https://image.freepik.com/free-photo/river-foggy-mountains-landscape_1204-511.jpg"*/}
        <CardContent className={classes.content}>
          <Typography
            className={"MuiTypography--heading"}
            variant={"h6"}
            gutterBottom
          >
            {titulo}
          </Typography>
          <Typography
            className={"MuiTypography--subheading"}
            variant={"caption"}
          >
          <InfoArea
              title={titulo}
              description={introduccion}
              icon={icono(icon)}
              iconColor="success"
              vertical
            />
          </Typography>
          {/*<Divider className={classes.divider} light />*/}
          {
                    Lista(lista,classesList)
          }
        </CardContent>
      </Card>
      <div className="clearfix"></div>
    </div>
  );
}
